<script>
  //import Thing from './Thing.svelte';
  import axios from 'axios'
  import { onMount } from 'svelte'
  import Form from './Form.svelte'
  import { DateTime as Dtime } from 'luxon';
  import { Base64 } from 'js-base64';


  //const url =
    //'http://localhost:8000/api/business/get/1f29b714-4e24-4c66-a54a-43cee5d7086c'
    //'http://localhost:8000/api/business/get/f2ea3d1e-cf46-4724-80d8-539a562ccabd'
  let justone = false

  let up = false
  let showform = false
  let formitems = { items: [] }
  let data = {
    active: 0,
    whatsapps: [],
  }
  let cont = true
  let personid = null

  const now = Dtime.local();
  let today = now.toISODate()




  onMount(() => {
    let key = ''
    if (document.currentScript.hasAttribute('data-key')) {
      key = document.currentScript.getAttribute('data-key')
    }
    else if(document.currentScript.hasAttribute('key')) {
      key = document.currentScript.getAttribute('key')
    }
    else{
      const queryString = document.currentScript.getAttribute('src').split("key=");
      key = queryString[1]
    }

    //key = "6fd735c9-1763-4c1b-b3c5-70771efb5e42"

    const url = "https://services.tochat.be/api/business/get/"+key

    axios
      .get(url)
      .then(function (response) {
        data = response.data.data
        //console.log(data)
        if (data.random === true) {
          data.whatsapps = shuffle(data.whatsapps)
          data.onlyactive = true
          justone = true
          //data.whatsapps = data.whatsapps.pop()

        }
        if (window.innerWidth < 767) {
          up = false
        } else {
          up = data.isopen
        }
      })
      .catch(function (error) {
        console.log(error)
      })
  })

  function shuffle(array) {
    let counter = array.length
    while (counter > 0) {
      let index = Math.floor(Math.random() * counter)
      counter--
      let temp = array[counter]
      array[counter] = array[index]
      array[index] = temp
    }
    return array
  }

  function close() {
    showform = false
  }

  function callmex(event) {

    let str = JSON.stringify(event.detail.data)
    str = Base64.encode(str);
    callme(personid, {}, str)
  }

  function callme(id, fitems, inputdata = {}) {

    personid = id

    let empty = true

     if(fitems != null && typeof fitems === 'object'){
       empty = Object.keys(fitems).length === 0 && fitems.constructor === Object
    }

    if (empty) {

      if (typeof window.fbq === 'function'){
        window.fbq('trackCustom', 'chatWithEvent');
      }

      if (typeof window.gtag === "function") {
        gtag('event', 'chatwith', {
          'event_category': 'widget',
          'event_label': 'click',
        });
        console.log("GA chatwith")
      }

      showform = false
      //window.location.href = 'https://services.tochat.be/api/business/send/' + id +"?data="+inputdata
      window.open('https://services.tochat.be/api/business/send/' + id +"?data="+inputdata)
    } else {
      showform = true
      formitems = fitems //9da8f2c8-3868-4a57-958c-96bccd4b2384
    }
  }

  function handleClick() {
    up = !up
  }

  function datecompare(optimes) {
    const days = {
      1: 'MO',
      2: 'TU',
      3: 'WE',
      4: 'TH',
      5: 'FR',
      6: 'SA',
      7: 'SU',
    }

    //console.log("POINT A")

    const day = now.weekday

    let localday;

    /*console.log("POINT A")
    console.log(Dtime.fromISO(today + "T10:10:10"))
    console.log("POINT B")
    //console.log(Dtime.fromISO(today + "T10:10:10").diff(Dtime.local()).values.milliseconds)
    console.log("POINT C")
    console.log(Dtime.fromISO(today + "T10:10:10").diffNow("minutes"))*/

    let online = false

    for (let i = 0; i < optimes.length; i++) {

      if(optimes[i].timezone){
        localday = Dtime.local().setZone(optimes[i].timezone,{keepLocalTime:false}).weekday
        today = Dtime.local().setZone(optimes[i].timezone,{keepLocalTime:false}).toISODate()

      }
      else{
        localday = day
      }

      /*console.log(localday)
      console.log(today)*/

      if (optimes[i].day == days[localday]) {

        //console.log("SAME DAY")

        let start = Dtime.fromISO(today + "T"+optimes[i].availableFrom).diffNow("minutes").minutes
        let fin = Dtime.fromISO(today + "T"+optimes[i].availableUntil).diffNow("minutes").minutes

        if(optimes[i].timezone){
          //console.log("TZ")
          let localtoday = Dtime.local().setZone(optimes[i].timezone,{keepLocalTime:false}).toISODate()
          start = Dtime.fromISO(localtoday + "T"+optimes[i].availableFrom).setZone(optimes[i].timezone,{keepLocalTime:true}).diffNow("minutes").minutes
          fin = Dtime.fromISO(localtoday + "T"+optimes[i].availableUntil).setZone(optimes[i].timezone,{keepLocalTime:true}).diffNow("minutes").minutes
        }

        if (start < 0 && fin > 0) {
          return true
        }
      }
    }
    return online

  }
</script>

